.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(0, 0, 0, 0.75);
}

.board {
  background: url(https://i.imgur.com/TH5DVEU.png);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 10px;
}

.modal {
  position: fixed;
  top: 30vh;
  left: 10%;
  width: 80%;
  z-index: 100;
  overflow: hidden;
}

.header {
  background: url(https://i.imgur.com/9J7vAMl.png);
  padding: 1rem;
}

.header h2 {
  margin: 0;
  color: white;
}

.content {
  padding: 1rem;
  display: inline-block;
}

.content p {
  color: white;
}

.actions {
  padding: 1rem;
  display: flex;
  justify-content: flex-end;
}

@media (min-width: 600px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}

.image {
  display: inline-block;
  width: 124px;
  height: 165.5px;
  vertical-align: top;
  margin-top: 2rem;
}
